</template>

<script>
export default {
  name: 'TopBar',
  props: {
    appInfo: String
  },
  data(){
    return {
      employee_data: "",
    }
  },
  mounted: function(){
    this.axios
      .post('http://authentication.mike.jtekt.maximemoreillon.com/personal_information')
      .then(response => this.employee_data = response.data)
      .catch(error => console.log(error))
  },
}
</script>

